.section {
   &__hero {
      background: url(..//img/hero-bg.png) center no-repeat;
      background-size: cover;
   }
}
.hero {
   height: auto;
   display: flex;
   position: relative;
   &__nav {
      max-width: 168px;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: column;
      border-right: 1px solid rgba(255, 255, 255, 0.25);
      @media (max-width: 992px) {
         max-width: 120px;
      }
   }
   .nav {
      position: relative;
      z-index: 0;
      @media (max-width: 768px) {
         position: absolute;
         transform: translate(-100%, 0);
         transition: all 0.3s ease;
         z-index: 999;
         &.active {
            transform: translate(0, 0);
            transition: all 0.3s ease;
         }
      }
      &::before {
         content: "go surf";
         font-weight: 800;
         font-size: 200px;
         line-height: 0.78;
         text-transform: uppercase;
         color: #ffffff;
         mix-blend-mode: normal;
         opacity: 0.07;
         position: absolute;
         left: 0;
         top: 0;
         max-width: 467px;
         width: 100%;
         @media (max-width: 992px) {
            font-size: 130px;
         }
         @media (max-width: 600px) {
            font-size: 80px;
         }
      }
      &__logo {
         font-weight: 800;
         font-size: 60px;
         line-height: 1.43;
         text-transform: uppercase;
         color: #ffffff;
         white-space: nowrap;
         transform: rotate(-90deg) !important;
         margin-top: 117px;
         position: relative;
         &::after {
            content: "";
            position: absolute;
            left: 105px;
            top: -40px;
            right: 0;
            background: #4af6cd;
            width: 191px;
            height: 167px;
            z-index: -1;
            transition: all 0.3s ease;
            @media (max-width: 992px) {
               height: 134px;
               
            }
         }
         &:hover::after {
            width: calc(100% + 45px);
            left: -20px;
            transition: all 0.3s ease;
            @media (max-width: 992px){
               left: -17px;
            }
         }
         @media (max-width: 992px) {
            font-size: 48px;
            margin-top: 100px;
         }
         span {
            font-weight: 250;
         }
      }
      &__menu {
         margin-top: 190px;
      }
      &__list {
         .nav__text {
            transition: all 0.3s ease;
            path {
               transition: all 0.3s ease;
            }
            &:hover {
               transition: all 0.3s ease;
               color: #4af6cd;
               path {
                  transition: all 0.3s ease;
                  fill: #4af6cd;
               }
            }
         }
         .nav__text:nth-child(2) {
            padding-bottom: 0;
         }
      }
      &__text {
         font-weight: 500;
         font-size: 12px;
         line-height: 14px;
         text-align: center;
         letter-spacing: 3px;
         color: #ffffff;
         display: flex;
         flex-direction: column;
         align-items: center;
         padding-bottom: 38px;
         img {
            margin-bottom: 6px;
         }
      }
      &__form {
         position: relative;
         display: flex;
         align-items: center;
         justify-content: center;
         padding: 33px 0 37px;
      }
      &__form-btn {
         background: none;
         border: none;
         cursor: pointer;
      }
      &__form-input {
         position: absolute;
         left: 80px;
         // top: -5px;
         border: none;
         border-bottom: 1px solid #ffffff;
         background: none;
         padding: 5px 10px !important;
         text-align: left !important;
         opacity: 0;
         &.active {
            opacity: 1;
         }
         &::placeholder {
            color: #ffffff;
         }
         &::-moz-placeholder {
            color: #ffffff;
            opacity: 1;
         }
      }
      &__date {
         text-align: center;
         padding-bottom: 39px;
      }
      &__date-title {
         font-weight: 800;
         font-size: 72px;
         line-height: 0.89;
         text-transform: uppercase;
         color: #ffffff;
         @media (max-width: 992px) {
            font-size: 58px;
         }
      }
      &__date-subtitle {
         font-weight: 300;
         font-size: 16px;
         line-height: 19px;
         letter-spacing: 1.4px;
         color: #ffffff;
         padding-bottom: 30px;
      }
      &__date-location {
         display: flex;
         align-items: center;
      }
      &__date-text {
         padding-bottom: 0 !important;
         padding-left: 9px;
      }
      .burger {
         @media (max-width: 768px) {
            position: absolute;
            left: 140px;
            top: 20px;
            display: block;
            width: 30px;
            height: 20px;
            cursor: pointer;
            z-index: 99 !important;
            &::after,
            &::before {
               content: "";
               width: 100%;
               height: 2px;
               border-radius: 2px;
               background: #4af6cd;
               position: absolute;
               left: 0;
               right: 0;
            }
            &::after {
               top: 0;
            }
            &::before {
               bottom: 0;
            }
            &.active-burger {
               z-index: 99 !important;
               &::after {
                  transform: translate(0, -50%) rotate(45deg);
                  top: 50%;
               }
               &::before {
                  transform: translate(0, 50%) rotate(-45deg);
                  bottom: 50%;
               }
               span {
                  display: none;
               }
            }
            span {
               position: absolute;
               width: 100%;
               height: 2px;
               border-radius: 2px;
               background: #4af6cd;
               top: 50%;
               transform: translate(0, -50%);
            }
         }
      }
   }
   &__body {
      width: 100%;
      padding: 600px 80px 35px 73px;
      z-index: 0;
      @media (max-width: 1110px) {
         padding: 500px 40px 35px 33px;
      }
      @media (max-width: 768px) {
         padding: 500px 20px 35px 20px;
      }
   }
   &__map {
      max-width: 1000px;
      width: 100%;
      position: absolute;
      right: 0;
      top: 0;
      z-index: -1;
      @media (max-width: 1110px) {
         max-width: 700px;
      }
      @media (max-width: 768px) {
         max-width: 100%;
      }
   }
   &__place {
      display: flex;
      align-items: center;
      position: absolute;
      @media (max-width: 768px) {
         display: block;
      }
      &.active {
         .hero__path {
            display: block;
            position: absolute;
            @media (max-width: 768px) {
               display: none;
            }
         }
         .hero__dot {
            width: 13px;
            height: 13px;
            transition: all 0.3s ease;
         }
         .hero__place-info {
            display: block;
            position: absolute;
            right: calc(100% + 10px);
            white-space: nowrap;
            @media (max-width: 768px) {
               right: auto;
               left: 50%;
               margin-top: 5px;
               transform: translate(-50%, 0);
            }
            @media (max-width: 480px) {
               font-size: 12px;
            }
         }
         &:nth-child(2) {
            .hero__path {
               left: 12.7px;
               bottom: -49px;
               img {
                  height: 198px;
               }
            }
         }
         &:nth-child(3) {
            .hero__path {
               left: 2px;
               transform: rotate(5deg);
               bottom: -4px;
               img {
                  height: 260px;
               }
            }
         }
         &:nth-child(4) {
            .hero__path {
               left: -20px;
               transform: rotate(12deg);
               bottom: 0px;
               img {
                  height: 300px;
               }
            }
         }
         &:nth-child(5) {
            .hero__path {
               left: -100px;
               transform: rotate(-11deg);
               bottom: 15px;
               img {
                  height: 385px;
               }
            }
         }
         @media (max-width: 1110px) {
            &:nth-child(2) {
               .hero__path {
                  left: 10px;
                  bottom: -20px;
                  transform: rotate(0deg);
                  img {
                     height: 99px;
                  }
               }
            }
            &:nth-child(3) {
               .hero__path {
                  left: -10px;
                  transform: rotate(-6deg);
                  bottom: 10px;
                  img {
                     height: 140px;
                  }
               }
            }
            &:nth-child(4) {
               .hero__path {
                  left: -50px;
                  transform: rotate(-11deg);
                  bottom: 10px;
                  img {
                     height: 200px;
                  }
               }
            }
            &:nth-child(5) {
               .hero__path {
                  left: -150px;
                  transform: rotate(-38deg);
                  bottom: 5px;
                  img {
                     height: 295px;
                  }
               }
            }
         }
      }
      &:nth-child(2) {
         top: 202px;
         left: 360px;
      }
      &:nth-child(3) {
         top: 302px;
         left: 540px;
      }
      &:nth-child(4) {
         top: 362px;
         left: 670px;
      }
      &:nth-child(5) {
         bottom: 24px;
         right: 260px;
      }
      @media (max-width: 1110px) {
         &:nth-child(2) {
            top: 142px;
            left: 250px;
         }
         &:nth-child(3) {
            top: 212px;
            left: 360px;
         }
         &:nth-child(4) {
            top: 262px;
            left: 470px;
         }
         &:nth-child(5) {
            bottom: 0px;
            right: 120px;
         }
      }
      @media (max-width: 768px) {
         &:nth-child(2) {
            top: 38.172043%;
            left: 33.2446808510638%;
         }
         &:nth-child(3) {
            top: 56.989247%;
            left: 47.8723404255319%;
         }
         &:nth-child(4) {
            top: 70.430108%;
            left: 62.5%;
         }
         &:nth-child(5) {
            bottom: 0px;
            right: 15.957447%;
         }
      }
   }
   &__dot {
      background: #4af6cd;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      transition: all 0.3s ease;
   }
   &__place-info {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #ffffff;
      display: none;
   }
   &__path {
      display: none; 
   }
   &__current {
      top: 56px;
      right: 127px;
      @media (max-width: 768px) {
         display: none;
      }
      .hero__place-info {
         display: block;
      }
      .hero__dot {
         width: 13px;
         height: 13px;
      }
   }
   &__tabs {
   }
   &__items {
      position: relative;
   }
   &__content {
      display: none;
      flex-direction: column;
      align-items: flex-end;
      text-align: right;
      transition: all 0.3s ease;
      &.active {
         display: flex;
         transition: all 0.3s ease;
      }
   }
   &__text {
      font-weight: 800;
      font-size: 40px;
      line-height: 2.15;
      text-transform: uppercase;
      color: #ffffff;
      @media (max-width: 480px) {
         font-size: 30px;
      }
   }
   &__next {
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         transform: translate(8px, 0);
      }
   }
   &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 30px;
      @media (max-width: 768px) {
         flex-direction: column-reverse;
         justify-content: center;
      }
   }
   &__header-items {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 100%;
      @media (max-width: 768px) {
         justify-content: center;
         padding-bottom: 30px;
      }
      @media (max-width: 480px) {
         flex-wrap: wrap;
      }
   }
   &__header-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 200px;
      width: 100%;
      margin-left: 40px;
      position: relative;
      cursor: pointer;
      @media (max-width: 1300px) {
         max-width: 170px;
         margin-left: 20px;
      }
      @media (max-width: 1110px) {
         max-width: 120px;
      }
      @media (max-width: 600px) {
         flex-direction: column;
         justify-content: center;
      }
      @media (max-width: 480px) {
         margin: 0px 10px 30px !important;
      }
      &::after {
         content: "";
         position: absolute;
         top: -8px;
         left: 0;
         right: 0;
         width: 100%;
         height: 4px;
         background: #ffffff;
         transition: all 0.3s ease;
      }
      &.active::after {
         background: #4af6cd;
         transition: all 0.3s ease;
      }
      &:first-child {
         margin-left: 0;
      }
   }
   &__header-title {
      font-weight: 800;
      font-size: 24px;
      line-height: 29px;
      text-transform: uppercase;
      color: #ffffff;
      @media (max-width: 480px) {
         font-size: 20px;
      }
   }
   &__header-text {
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #ffffff;
      @media (max-width: 480px) {
         font-size: 14px;
      }
   }
   .map {
      width: 100%;
      img {
         width: 100%;
      }
   }
   .scroll {
      cursor: pointer;
   }
   &__buttons {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 40px;
      right: 40px;
      @media (max-width: 768px) {
         top: 20px;
         right: 20px;
      }
   }
   &__btn-prev {
      transform: rotate(-180deg);
      margin: 0 20px 5px 0;
   }
}
