*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-style: normal;
	font-family: "Fira Sans", sans-serif;
	background: #000;
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	padding: 0 70px 0 133px;
	@media (max-width: $md1) {
		padding: 0 70px 0 133px;
	}
	@media (max-width: $md2) {
		padding: 0 70px;
	}
	@media (max-width: $md3) {
		padding: 0 40px;
	}
	@media (max-width: $md4) {
		padding: 0 10px;
	}
}
//main
.title {
	position: relative;
	font-weight: 800;
	font-size: 80px;
	line-height: 1.95;
	text-transform: uppercase;
	color: #ffffff;
	width: 100%;
	margin-left: 112px;
	&::after {
		content: "";
		position: absolute;
		width: 80px;
		height: 1px;
		right: 0;
		top: 35px;
		background: #4af6cd;
	}
	@media (max-width: 1110px) {
		font-size: 64px;
		&::after {
			top: 25px;
		}
	}
	@media (max-width: 768px) {
		margin-left: 0;
	}
	@media (max-width: 480px) {
		font-size: 48px;
		&::after {
			top: 15px;
		}
	}
	@media (max-width: 320px) {
		font-size: 40px;
	}
}
.title-big {
	font-weight: 800;
	font-size: 40px;
	line-height: 2.15;
	text-align: right;
	text-transform: uppercase;
	color: #ffffff;
	span {
		font-weight: 250;
	}
}
.subtitle {
	font-weight: 400;
	font-size: 22px;
	line-height: 1.45;
	letter-spacing: 7.33333px;
	text-transform: uppercase;
	color: #ffffff;
	mix-blend-mode: normal;
	opacity: 0.5;
	position: relative;
	&::after {
		content: "";
		position: absolute;
		left: -111px;
		top: 50%;
		transform: translate(0, -50%);
		background: #ffffff;
		width: 80px;
		height: 1px;
	}
	@media (max-width: 480px) {
		font-size: 18px;
		&::after {
			width: 60px;
			left: -81px;
		}
	}
}
// page animation
.js-scroll {
	opacity: 0;
	transition: opacity 0ms;
	&.scrolled {
		opacity: 1;
		&.fade-in {
			animation: fade-in 1s ease-in-out 0s;
		}
		&.slide-in-left {
			animation: slide-in-left 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: slide-in-top 0.6s ease-in-out 0s;
			}
		}
		&.slide-in-top {
			animation: slide-in-top 1s ease-in-out 0s;
		}
		&.slide-in-bottom {
			animation: slide-in-bottom 1s ease-in-out 0s;
		}
		&.slide-in-right {
			animation: slide-in-right 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: fade-in-top 0.6s ease-in-out 0s;
			}
		}
		&.slide-nav-left {
			animation: slide-nav-left 1s ease-in-out 0s;
		}
	}
}
@keyframes slide-nav-left {
	0% {
		transform: translateX(-100%);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes slide-in-left {
	0% {
		transform: translateX(-100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes slide-in-top {
	0% {
		transform: translateY(-100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-bottom {
	0% {
		transform: translateY(100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-right {
	0% {
		transform: translateX(100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media (max-width: 480px) {
	@keyframes slide-in-left {
		0% {
			transform: translateX(-50px);
			opacity: 0;
		}
		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-top {
		0% {
			transform: translateY(-50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-bottom {
		0% {
			transform: translateY(50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-right {
		0% {
			transform: translateX(50px);
			opacity: 0;
		}
		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
}
