footer {
   padding-bottom: 34px;
}
.footer {
   display: flex;
   flex-direction: column;
   align-items: center;
   &__logo {
      position: relative;
      margin-bottom: 27px;
      @media (max-width: 400px) {
         font-size: 30px !important;
      }
      &::after {
         content: "";
         position: absolute;
         right: -10px;
         top: 50%;
         transform: translate(0, -50%);
         background: #4af6cd;
         width: 112px;
         height: 80px;
         z-index: -1;
         transition: all 0.3s ease;
         @media (max-width: 1249px) {
            width: 120px;
         }
         @media (max-width: 320px) {
            right: -10px;
         }
      }
      &:hover {
         &::after {
            width: calc(100% + 20px);
            transition: all 0.3s ease;
            @media (max-width: 320px) {
               width: calc(100% + 10px);
            }
         }
      }
   }
   &__text {
      font-weight: 300;
      font-size: 12px;
      line-height: 14px;
      text-align: center;
      letter-spacing: 1.05px;
      text-transform: uppercase;
      color: #ffffff;
      mix-blend-mode: normal;
      opacity: 0.5;
   }
}
