.section {
   &__shop {
      padding: 0 0 293px;
      @media (max-width: 768px) {
         padding: 0 0 150px;
      }
   }
}
.shop {
   &__title {
      max-width: 206px;
      @media (max-width: 1110px) {
         max-width: 170px;
      }
      @media (max-width: 480px) {
         max-width: 125px;
      }
      @media (max-width: 320px) {
         max-width: 100px;
      }
   }
   &__swiper {
      .swiper__buttons {
         display: flex;
         align-items: center;
         position: absolute;
         top: 0;
         right: 0;
         z-index: 99;
      }
      .swiper__btn {
         mix-blend-mode: normal;
         opacity: 0.25;
         cursor: pointer;
      }
      .swiper-prev {
         transform: rotate(-180deg);
         margin-bottom: 8px;
         margin-right: 20px;
      }
      .slide {
         position: relative;
         z-index: 0;
         display: flex;
         justify-content: space-between;
         align-items: center;
         height: 700px;
         @media (max-width: 1300px) {
            //flex-wrap: wrap;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            height: auto;
         }
         &__bg {
            position: absolute;
            right: 0;
            top: 0;
            width: auto;
            height: 590px;
            z-index: -1;
            img {
               width: 100%;
               height: 100%;
            }
         }
         &__content {
            z-index: 2;
            @media (max-width: 1300px) {
               margin-bottom: 100px;
            }
         }
         &__subtitle {
            max-width: 90px;
            &::after {
               right: -111px !important;
               left: auto;
               @media (max-width: 480px) {
                  right: -80px !important;
               }
            }
         }
         &__title {
            text-align: left !important;
            max-width: 345px;
            width: 100%;
            line-height: 1 !important;
            margin: 14px 0 43px;
            @media (max-width: 1300px) {
               max-width: 100%;
            }
            @media (max-width: 480px) {
               font-size: 30px !important;
               margin: 14px 0 23px;
            }
         }
         &__rate {
            display: flex;
            align-items: center;
         }
         &__price {
            font-weight: 800;
            font-size: 32px;
            line-height: 1.25;
            text-transform: uppercase;
            color: #ffffff;
            margin-bottom: 68px;
            @media (max-width: 480px) {
               margin-bottom: 30px;
            }
            sup {
               font-size: 18px;
            }
         }
         &__btn {
            position: relative;
            margin-left: 10px;
            @media (max-width: 400px) {
               font-size: 30px !important;
            }
            &::after {
               content: "";
               position: absolute;
               right: -10px;
               top: 50%;
               transform: translate(0, -50%);
               background: #4af6cd;
               width: 112px;
               height: 80px;
               z-index: -1;
               transition: all 0.3s ease;
               @media (max-width: 1249px) {
                  width: 120px;
               }
               @media (max-width: 320px) {
                  right: -10px;
               }
            }
            img {
               margin-left: 16px;
               transition: all 0.3s ease;
               @media (max-width: 1249px) {
                  margin-left: 8px;
               }
            }
            &:hover {
               &::after {
                  width: calc(100% + 20px);
                  transition: all 0.3s ease;
                  @media (max-width: 320px) {
                     width: calc(100% + 10px);
                  }
               }
               img {
                  transform: translate(5px, 0);
                  transition: all 0.3s ease;
               }
            }
         }
         &__item {
            position: absolute;
            z-index: 1;
            left: 50%;
            transform: translate(-50%, 0);
            top: 0;
            bottom: 0;
            max-width: 1080px;
            width: 100%;
            @media (max-width: 1300px) {
               position: relative;
               //max-width: 649px;
               width: 100%;
               left: auto;
               transform: translate(0, 0);
            }
         }
         &__item-bg {
            max-width: 228px;
            width: 100%;
            height: auto;
            z-index: 0;
            margin: 0 auto;
            position: relative;
            img {
               width: 100%;
               height: 100%;
               z-index: -1;
            }
         }
         &__item-container {
            position: absolute;
            display: flex;
            align-items: center;
            z-index: 98;
            @media (max-width: 992px) {
               //position: relative;
               top: -50px !important;
               left: 0 !important;
               bottom: auto !important;
            }
            &:nth-child(2) {
               top: 188px;
               //left: 980px;
               left: 58.083885%;
               @media (max-width: 1110px) {
                  left: 60.083885%;
               }
            }
            &:nth-child(3) {
               top: 280px;
               //left: 430px;
               left: 3.730684%;
               flex-direction: row-reverse;
               @media (max-width: 1249px) {
                  left: 2.730684%;
               }
               @media (max-width: 1110px) {
                  left: -2.730684%;
               }
               .slide__item-text {
                  margin-right: 112px;
                  margin-left: 0;
                  &::after {
                     left: auto;
                     right: -104px !important;
                  }
                  @media (max-width: 1110px) {
                     margin-right: 80px;
                     &::after {
                        right: -80px !important;
                     }
                  }
                  @media (max-width: 992px) {
                     margin: 0;
                  }
               }
            }
            &:nth-child(4) {
               bottom: 55px;
               //left: 490px;
               left: 9.041943%;
               @media (max-width: 1249px) {
                  left: 8.041943%;
               }
               @media (max-width: 1110px) {
                  left: 5.041943%;
               }
               .slide__item-text {
                  margin-right: 112px;
                  margin-left: 0;
                  &::after {
                     left: auto;
                     right: -104px !important;
                  }
                  @media (max-width: 1110px) {
                     margin-right: 80px;
                     &::after {
                        right: -80px !important;
                     }
                  }
                  @media (max-width: 992px) {
                     margin: 0;
                  }
               }
            }
            &.active {
               .slide__item-text {
                  opacity: 1;
                  visibility: visible;
                  transition: all 0.3s ease;
                  @media (max-width: 992px) {
                     display: block;
                  }
               }
            }
         }
         &__item-text {
            font-weight: 800;
            font-size: 24px;
            line-height: 1;
            text-transform: uppercase;
            color: #ffffff;
            max-width: 255px;
            width: 100%;
            margin-left: 112px;
            position: relative;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease;
            @media (max-width: 1110px) {
               margin-left: 80px;
            }
            @media (max-width: 992px) {
               max-width: 100%;
            }
            &::after {
               content: "";
               position: absolute;
               top: 50%;
               transform: translate(0, -50%);
               width: 80px;
               height: 1px;
               left: -104px;
               z-index: -1;
               background: #ffffff;
               @media (max-width: 1110px) {
                  width: 60px;
                  left: -80px;
               }
            }
            @media (max-width: 992px) {
               display: none;
               margin-left: 0;
               &::after {
                  display: none;
               }
            }
         }
         &__item-header {
            width: 20px;
            height: 20px;
            background: #ffffff;
            border-radius: 50%;
            position: absolute;
            cursor: pointer;
            z-index: 101;
            &:nth-child(2) {
               top: 202px;
               right: 36px;
            }
            &:nth-child(3) {
               top: 295px;
               left: -5px;
            }
            &:nth-child(4) {
               bottom: 65px;
               left: 53px;
            }
            &::after {
               content: "";
               position: absolute;
               width: 12px;
               height: 3px;
               left: 50%;
               top: 50%;
               transform: translate(-50%, -50%);
               background: #4af6cd;
            }
            &::before {
               content: "";
               position: absolute;
               width: 3px;
               height: 12px;
               left: 50%;
               top: 50%;
               transform: translate(-50%, -50%);
               background: #4af6cd;
               transition: all 0.3s ease;
            }
            &.active::before {
               transform: translate(-50%, -50%) rotate(-90deg);
               transition: all 0.3s ease;
            }
         }
         &__extras {
            margin-top: auto;
            z-index: 1;
            @media (max-width: 1300px) {
               margin: 50px auto 0;
            }
         }
         &__extras-title {
            font-weight: 800;
            font-size: 28px;
            line-height: 1.43;
            text-transform: uppercase;
            color: #ffffff;
            margin-bottom: 28px;
            @media (max-width: 1300px) {
               text-align: center;
            }
         }
         &__extras-items {
            display: flex;
            justify-content: center;
            @media (max-width: 400px) {
               flex-direction: column;
            }
         }
         &__extras-item {
            text-align: center;
            margin-right: 65px;
            @media (max-width: 400px) {
               margin-right: 0;
               margin-bottom: 40px;
               &:last-child {
                  margin-bottom: 0;
               }
            }
            &:last-child {
               margin-right: 0;
            }
         }
         &__extras-subtitle {
            margin: 36px 0 4px;
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 8px;
            text-transform: uppercase;
            color: #ffffff;
            mix-blend-mode: normal;
            opacity: 0.5;
         }
         &__extras-img {
            height: 105px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
         }
         &__extras-text {
            font-weight: 800;
            font-size: 18px;
            line-height: 2.22;
            text-transform: uppercase;
            color: #ffffff;
         }
      }
   }
}
