.section {
   &__surf {
      padding: 127px 0 217px;
   }
}
.surf {
   overflow: hidden;
   //position: relative;
   &__title {
      max-width: 187px;
      margin-bottom: 26px;
      @media (max-width: 1110px){
         max-width: 152px;
      }
      @media (max-width: 480px){
         max-width: 113px;
      }
      @media (max-width: 350px){
         max-width: 94px;
      }
   }
   &__tabs {
      position: relative;
      height: 100%;
   }
   &__map {
      position: relative;
      width: 100%;
      z-index: -1;
      height: 100%;
      @media (max-width: 1300px) {
         display: none;
      }
      img {
         width: 100%;
         height: 100%;
      }
   }
   &__map-title {
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      letter-spacing: 8px;
      text-transform: uppercase;
      color: #ffffff;
      position: absolute;
      right: 0;
      top: calc(100% - 22px / 2);
      width: 22px;
      //height: 22px;
      white-space: nowrap;
      transform: rotate(-90deg);
      mix-blend-mode: normal;
      opacity: 0.5;
   }
   &__content {
      flex-direction: column;
      align-items: flex-end;
      display: none;
      position: relative;
      z-index: 99;
      &.active {
         display: flex;
      }
   }
   &__content-title {
   }
   &__content-text {
   }
   &__headers {
      @media (max-width: 1300px) {
         display: flex;
         justify-content: center;
         align-items: center;
         flex-wrap: wrap;
      }
   }
   &__header {
      max-width: 283px;
      width: 100%;
      position: absolute;
      z-index: 99;
      @media (max-width: 1300px) {
         position: relative;
         left: 0 !important;
         right: 0 !important;
         top: 0 !important;
         padding: 20px;
         &:hover {
            .surf__header-content {
               border: 1px solid #4af6cd;
            }
         }
      }
      &:hover {
         .surf__header-content {
            opacity: 1;
            visibility: visible;
            transition: all 0.3s ease;
         }
         .surf__header-dot {
            transform: scaleY(1.375);
            transition: all 0.3s ease;
         }
      }
      &:first-child {
         left: 0px;
         //top: 270px;
         top: 28.037383%;
         .surf__header-dot {
            margin-left: 0 !important;
            @media (max-width: 1300px) {
               margin: 11px auto 0 !important   ;
            }
         }
      }
      &:nth-child(2) {
         left: 5.518764%;
         //top: 220px;
         top: 22.845275%;
      }
      &:nth-child(3) {
         left: 19.315673%;
         //top: 280px;
         top: 29.075805%;
      }
      &:nth-child(4) {
         left: 26.49006%;
         //top: 530px;
         top: 55.036345%;
      }
      &:nth-child(5) {
         left: 41.390728%;
         //top: 80px;
         top: 8.307373%;
      }
      &:nth-child(6) {
         left: 46.357616%;
         //top: 600px;
         top: 62.305296%;
      }
      &:nth-child(7) {
         right: 24.282561%;
         //top: 350px;
         top: 36.344756%;
      }
      &:nth-child(8) {
         right: 5.518764%;
         //top: 610px;
         top: 63.34433718%;
      }
   }
   &__header-dot {
      background: #4af6cd;
      box-shadow: 0px 0px 35px rgba(74, 246, 205, 0.837777);
      width: 24px;
      height: 24px;
      border-radius: 50%;
      margin: 11px auto 0;
      transition: all 0.3s ease;
      cursor: pointer;
   }
   &__header-content {
      background: rgba(15, 16, 16, 0.7);
      border-radius: 6px;
      padding: 24px 32px 31px;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
      @media (max-width: 1300px) {
         opacity: 1;
         visibility: visible;
      }
   }
   &__header-header {
      display: flex;
      justify-content: space-between;
      margin-bottom: 26px;
   }
   &__header-container {
   }
   &__header-title {
      font-weight: 800;
      font-size: 18px;
      line-height: 22px;
      text-transform: uppercase;
      color: #ffffff;
      position: relative;
      padding-bottom: 13px;
      margin-bottom: 8px;
      &::after {
         content: "";
         width: 80px;
         height: 1px;
         background: #4af6cd;
         position: absolute;
         bottom: 0;
         left: 0;
      }
   }
   &__header-subtitle {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #ffffff;
   }
   &__header-items {
      display: flex;
   }
   &__header-item {
      margin-right: 42px;
      text-align: center;
      &:last-child {
         margin-right: 0;
      }
   }
   &__header-subtext {
      font-weight: 300;
      font-size: 10px;
      line-height: 12px;
      text-transform: uppercase;
      color: #ffffff;
   }
   &__swiper {
      margin: 57px 0 0;
      &.swiper {
         overflow: unset;
         .swiper__buttons {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            margin-bottom: 64px;
         }
         .swiper__btn {
            mix-blend-mode: normal;
            opacity: 0.25;
            cursor: pointer;
         }
         .swiper-prev{
            transform: rotate(-180deg);
            margin-right: 20px;
            margin-bottom: 8px;
         }
      }
      .slide {
         height: 750px;
         position: relative;
         padding: 80px 40px;
         z-index: 0;
         transition: all 0.3s ease;
         @media (max-width: 1249px) {
            padding: 60px 20px;
         }
         @media (max-width: 400px) {
            height: 600px;
         }
         &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: rgba(28, 33, 33, 0.5);
            z-index: -1;
            transition: all 0.3s ease;
         }
         &:hover {
            margin-top: -32px;
            transition: all 0.3s ease;
            &::after {
               opacity: 0;
               transition: all 0.3s ease;
            }
            .slide__btn {
               opacity: 1;
               transition: all 0.3s ease;
            }
         }
         &__bg {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
         }
         &__title {
            font-weight: 800;
            font-size: 40px;
            line-height: 0.9;
            text-transform: uppercase;
            color: #ffffff;
         }
         &__subtitle {
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 8px;
            text-transform: uppercase;
            color: #ffffff;
            position: absolute;
            bottom: 80px;
            left: 40px;
            width: 22px;
            white-space: nowrap;
            transform: rotate(-90deg);
            @media (max-width: 1249px) {
               left: 20px;
            }
            @media (max-width: 320px) {
               left: 10px;
            }
         }
         &__btn {
            position: absolute;
            z-index: 1;
            bottom: 0px;
            right: 28px;
            opacity: 0;
            transition: all 0.3s ease;
            @media (max-width: 1249px) {
               font-size: 30px;
               bottom: 7px;
            }
            &::after {
               content: "";
               position: absolute;
               right: -20px;
               top: 50%;
               transform: translate(0, -50%);
               background: #4af6cd;
               width: 168px;
               height: 80px;
               z-index: -1;
               transition: all 0.3s ease;
               @media (max-width: 1249px) {
                  width: 120px;
               }
               @media (max-width: 320px) {
                  right: -10px;
               }
            }
            img {
               margin-left: 16px;
               transition: all 0.3s ease;
               @media (max-width: 1249px) {
                  margin-left: 8px;
               }
            }
            &:hover {
               &::after {
                  width: calc(100% + 40px);
                  transition: all 0.3s ease;
                  @media (max-width: 320px) {
                     width: calc(100% + 20px);
                  }
               }
               img {
                  transform: translate(5px, 0);
                  transition: all 0.3s ease;
               }
            }
         }
      }
   }
}
