.section {
   &__sleep {
      padding: 0 0 327px;
      @media (max-width: 768px){
         padding: 0 0 250px;
      }
   }
}
.sleep {
   position: relative;
   &__title {
      max-width: 220px;
      margin-bottom: 50px;
      @media (max-width: 992px){
         max-width: 180px;
      }
      @media (max-width: 480px){
         max-width: 135px;
      }
      @media (max-width: 320px){
         max-width: 110px;
      }
   }
   &__swiper {
      &.swiper {
         position: unset;
         overflow: unset;
      }
      .swiper__buttons {
         display: flex;
         align-items: center;
         position: absolute;
         top: 330px;
         right: 0;
         z-index: 99;
      }
      .swiper__btn {
         mix-blend-mode: normal;
         opacity: 0.25;
         cursor: pointer;
      }
      .swiper-prev{
         transform: rotate(-180deg);
         margin-bottom: 8px;
         margin-right: 20px;
      }
      .slide {
         position: relative;
         z-index: 0;
         &__bg {
            position: absolute;
            left: 0;
            top: 0;
            width: auto;
            height: 780px;
            z-index: -1;
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
         }
         &__header {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            margin-bottom: 447px;
            @media (max-width: 768px) {
               margin-bottom: 300px;
            }
            @media (max-width: 400px) {
               margin-bottom: 200px;
            }
         }
         &__subtitle {
         }
         &__title {
            @media (max-width: 600px) {
               font-size: 30px !important;
            }
            @media (max-width: 400px) {
               line-height: 1 !important;
            }
         }
         &__info {
            width: 100%;
            &::after {
               content: url(..//img/sleep-gam.webp);
               position: absolute;
               right: -120px;
               top: 400px;
               width: 566px;
               height: 386px;
               @media (max-width: 1110px) {
                  top: 350px;
                  right: -250px;
               }
               @media (max-width: 768px) {
                  top: 150px;
                  //right: -350px;
               }
               @media (max-width: 480px) {
                  display: none;
               }
            }
            &::before {
               content: url(..//img/sleep-frangipani.webp);
               position: absolute;
               right: -158px;
               bottom: -200px;
               width: 342px;
               height: 265px;
               @media (max-width: 480px) {
                  display: none;
               }
            }
         }
         &__info-container{
            display: flex;
            align-items: center;
            @media (max-width: 480px){
               flex-direction: column;
               align-items: flex-start;
            }
         }
         &__info-rate{
            display: flex;
            align-items: center;
            margin-left: 25px;
            @media (max-width: 480px){
               margin-left: 0;
            }
         }
         &__info-star{
            margin-right: -11px;
            @media (max-width: 480px){
               margin-top: -30px;
            }
            @media (max-width: 400px){
               margin-top: 0;
            }
            &:last-child{
               margin-right: 0;
            }
         }
         &__info-header {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 102px;
            @media (max-width: 480px) {
               margin-bottom: 52px;
            }
         }
         &__info-subtitle {
            &::after {
               left: auto;
               right: -111px;
               @media (max-width: 480px) {
                  right: -81px;
               }
            }
         }
         &__info-title {
            @media (max-width: 400px) {
               line-height: 1 !important;
               font-size: 30px !important;
            }
         }
         &__info-items {
            display: flex;
            @media (max-width: 1249px) {
               flex-wrap: wrap;
               justify-content: center;
            }
         }
         &__info-input{
            background: none;
            border: none;
            appearance: none !important;
            max-width: 40px;
            height: 40px;
            @media (max-width: 400px) {
               line-height: 1 !important;
               font-size: 30px !important;
               height: 30px;
            }
         }
         &__info-buttons{
            display: flex;
            align-items: center;
            margin-left: 180px;
            margin-top: 13px;
            @media (max-width: 992px){
               margin-left: 145px;
            }
         }
         &__info-number{
            position: relative;
            width: 14px;
            height: 14px;
            cursor: pointer;
            &:first-child{
               margin-right: 14px;
               &::after{
                  display: none;
               }
            }
            &::after{
               content: '';
               position: absolute;
               top: 0;
               left: 50%;
               transform: translate(-50%, 0);
               height: 100%;
               width: 3px;
               background: #4AF6CD;
            }
            &::before{
               content: '';
               position: absolute;
               top: 50%;
               left: 0;
               transform: translate(0, -50%);
               height: 3px;
               width: 100%;
               background: #4AF6CD;
            }
         }
         &__info-item {
            flex: 0 1 25%;
            position: relative;
            height: 180px;
            padding-right: 44px;
            &:nth-child(1) {
               height: 105px;
            }
            &:nth-child(3) {
               height: 185px;
            }
            &:nth-child(4) {
               height: 115px;
               padding-right: 0;
            }
            @media (max-width: 1249px) {
               padding: 20px !important;
               flex: 0 1 50%;
               height: 200px;
               &:nth-child(2) {
                  height: 150px;
               }
               &:nth-child(4) {
                  height: 140px;
               }
            }
            @media (max-width: 600px) {
               flex: 0 1 80%;
            }
            @media (max-width: 480px) {
               flex: 0 1 100%;
            }
         }
         &__info-subtext {
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 8px;
            text-transform: uppercase;
            color: #ffffff;
            mix-blend-mode: normal;
            opacity: 0.5;
            transform: rotate(-90deg);
            width: 22px;
            position: absolute;
            left: 0;
            bottom: 0;
            white-space: nowrap;
         }
         &__info-text {
            text-align: left !important;
            max-width: 242px;
            width: 100%;
            line-height: 0.9;
            margin-left: 37px;
            @media (max-width: 992px) {
               font-size: 30px !important;
            }
         }
         &__info-label {
            font-weight: 400;
            font-size: 14px;
            line-height: 2.57;
            text-align: left;
            letter-spacing: 5.25px;
            text-transform: uppercase;
            color: #ffffff;
            mix-blend-mode: normal;
            opacity: 0.75;
            margin-left: 110px;
            @media (max-width: 992px) {
               margin-left: 60px;
            }
         }
         &__info-button {
            display: flex;
            justify-content: center;
            margin-top: 78px;
         }
         &__info-btn {
            position: relative;
            @media (max-width: 400px) {
               font-size: 30px !important;
            }
            &::after {
               content: "";
               position: absolute;
               right: -20px;
               top: 50%;
               transform: translate(0, -50%);
               background: #4af6cd;
               width: 168px;
               height: 80px;
               z-index: -1;
               transition: all 0.3s ease;
               @media (max-width: 1249px) {
                  width: 120px;
               }
               @media (max-width: 320px) {
                  right: -10px;
               }
            }
            img {
               margin-left: 16px;
               transition: all 0.3s ease;
               @media (max-width: 1249px) {
                  margin-left: 8px;
               }
            }
            &:hover {
               &::after {
                  width: calc(100% + 40px);
                  transition: all 0.3s ease;
                  @media (max-width: 320px) {
                     width: calc(100% + 20px);
                  }
               }
               img {
                  transform: translate(5px, 0);
                  transition: all 0.3s ease;
               }
            }
         }
      }
   }
}
